.title__main {
  font-style: normal;
  font-weight: 700;
  line-height: 1.2;
  font-size: 28px;
  max-width: 286px;
  @include tablet {
    font-size: 48px;
    max-width: 680px;
  }
  @include desktop-first {
    font-size: 64px;
    max-width: 586px;
  }
  @include desktop {
    font-size: 86px;
    max-width: 747px;
  }
}
